  components: {
    description,
  },
  data: function () {
    return {
      id: 0,
      Data: Json,
      DataVideo: "",
    };
  },
  created() {
    this.id = this.$attrs.id;
    this.DataVideo = 0;
    for (let i = 0; i < this.Data.length; i++) {
      if (this.Data[i].Id == this.id) {
        this.DataVideo = this.Data[i];
      }
    }
    document.title = this.DataVideo.title;
  },
};
