<template>
  <div>
    <router-view></router-view>
  </div>
</template>
<script>
import Json from "@/Data/Videos.json";
// import Video from "@/components/Video.vue";
export default {
  name: "Watch",
  // components: { Video },
  data: function () {
    return {
      id: 0,
      Data: Json,
      DataVideo: "",
    };
  },
  created() {
    this.id = this.$route.params.id;
    this.DataVideo = 0;
    for (let i = 0; i < this.Data.length; i++) {
      if (this.Data[i].Id == this.id) {
        this.DataVideo = this.Data[i];
      }
    }
    document.title = this.DataVideo.title;
  },
};
</script>
<style scoped>
@import url("../assets/Styles.css");
</style>
