<template>
  <div class="description">
    <p style="white-space: pre-line">{{ Edit }}</p>
    <button v-if="description.length > length" @click="Toggle">
      {{ Button }}
    </button>
  </div>
</template>
<script>
export default {
  name: "description",
  props: ["description", "length"],
  data: function () {
    return {
      Button: "show",
      Edit: 0,
    };
  },
  methods: {
    Toggle: function () {
      if (this.Button == "show") {
        this.Button = "hidde";
        this.Edit = this.description.slice(0, this.description.length);
      } else {
        this.Button = "show";
        this.Edit = this.description.slice(0, this.length) + "...";
      }
    },
  },
  created() {
    this.Edit =
      this.description.length > this.length
        ? this.description.slice(0, this.length) + "..."
        : this.description;
  },
};
</script>
